import React, { Fragment } from "react";
import smartContranctImg from "../../styles/assets/images/smartcontractdev/smartcontract1.jpg";
import smartContranctImg2 from "../../styles/assets/images/smartcontractdev/smartcontract2.jpg";
import smartContranctIm3 from "../../styles/assets/images/smartcontractdev/smartcontract3.jpg";
import { useEffect } from "react";
import { useState } from "react";

const DevelopmentServices = () => {
  let currentIndex = 0;
  let isPaused = false;

  const [idx, setIdx] = useState("");

  function showSlide(index) {
    const carouselItems = document.querySelectorAll(".carousel-item");
    if (index < 0) {
      index = carouselItems.length - 1;
    } else if (index >= carouselItems.length) {
      index = 0;
    }
    setIdx(index);
    currentIndex = index;
  }

  function autoSlide() {
    showSlide(currentIndex + 1);
  }

  useEffect(() => {
    const carousel = document.querySelector(".carousel");
    carousel?.addEventListener("mouseover", () => {
      isPaused = true;
    });

    carousel?.addEventListener("mouseout", () => {
      isPaused = false;
    });

    setInterval(autoSlide, 3000);
  }, []);

  return (
    <Fragment>
      <section className='section-webdev'>
        <div className='container'>
          <h3 className='heading-tertiary'>
            SMART CONTRACT DEVELOPMENT SERVICES
          </h3>
          <p className='description'>
            Looking to take advantage of the revolutionary potential of
            blockchain technology? Our team of expert developers can help you
            create and implement smart contracts that can streamline your
            business processes, improve transparency and security, and unlock
            new opportunities for growth and innovation..
          </p>
        </div>
      </section>
      <section className='section-blog'>
        <div className='container'>
          <h3 className='heading-tertiary'>
            Unlock the Potential of Blockchain Technology with Our Expert Team
          </h3>
          <div className='container'>
            <div className='carousel-container'>
              <div
                className='carousel'
                style={{
                  transform: `translateX(-${idx * 100}%)`,
                }}>
                <div className='carousel-item'>
                  <div className='grid grid--2-cols'>
                    <div className='content-box-flex'>
                      <p className='description'>
                        <strong>
                          Custom smart contract programming: Our developers have
                          extensive experience working with Ethereum and other
                          smart contract platforms, and can help you create
                          smart contracts that are tailored to your specific
                          needs and requirements.
                        </strong>
                      </p>
                      <p className='description'>
                        <strong>
                          Smart contract audit: Our team can review and test
                          your smart contracts to ensure that they are secure
                          and efficient, and can identify and fix any potential
                          vulnerabilities.
                        </strong>
                      </p>
                    </div>
                    <div className='img-slide-box'>
                      <img
                        className='web-img'
                        src={smartContranctImg}
                        alt='fullstack devlopment example'
                      />
                    </div>
                  </div>
                </div>
                <div className='carousel-item'>
                  <div className='grid grid--2-cols'>
                    <div className='content-box-flex'>
                      <p className='description'>
                        <strong>
                          Decentralized application (dApp) development: We can
                          help you create decentralized applications (dApps)
                          that run on smart contract platforms, and can unlock
                          new possibilities for your business.
                        </strong>
                      </p>
                      <p className='description'>
                        <strong>
                          Decentralized finance (DeFi) development: We can help
                          you create decentralized finance (DeFi) applications
                          that can improve the efficiency and security of your
                          financial transactions.
                        </strong>
                      </p>
                    </div>
                    <div className='img-slide-box'>
                      <img
                        className='web-img'
                        src={smartContranctImg2}
                        alt='fullstack devlopment example'
                      />
                    </div>
                  </div>
                </div>
                <div className='carousel-item'>
                  <div className='grid grid--2-cols'>
                    <div className='content-box-flex'>
                      <p className='description'>
                        <strong>
                          Smart contract security: Our team can help you
                          implement best practices and security protocols to
                          ensure that your smart contracts are protected against
                          hacking and other types of cyber attacks.
                        </strong>
                      </p>
                      <p className='description'>
                        <strong>
                          Smart contract deployment: We deploy your smart
                          contract to the Ethereum blockchain, making it live
                          and accessible to users.
                        </strong>
                      </p>
                    </div>
                    <div className='img-slide-box'>
                      <img
                        className='web-img'
                        src={smartContranctIm3}
                        alt='fullstack devlopment example'
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </Fragment>
  );
};

export default DevelopmentServices;
